.text-light {
    color: white;
    font-size: 30px;
}

.ListBuletNone{
    list-style-type: none;
}


.TS{
    color: #007FFF;
}

.questions{
    position: relative;
    top: 50px;
}

.radio:hover {
    background-color: white;
    padding: 0.05em 0.05em 0.05em 0.05em;
    border-radius: 99em;
    transition: 0.25s ease;
}

.radio{
    display: inline-flex;
    align-items: center;
    cursor: pointer;
    margin-right: 10px;
    font-size: 25px;
    margin-top: 20px;
}

.radio__input{
    display: none;
}

.radio__radio{
    width: 2em;
    height: 2em;
    border: 3px solid #007FFF;
    border-radius: 50%;
    margin-right: 10px;
    box-sizing: border-box;
    padding: 2px;
}

.radio__radio::after{
    content: "";
    width: 100%;
    height: 100%;
    display: block;
    background: #007FFF;
    border-radius: 50%;

    transform: scale(0);
    transition: transform 0.25s;
}

.radio__input:checked + .radio__radio::after{
    transform: scale(1);
}

.checked::before{
    transform: scale(0);
}

.checked::after{
    transform: scale(1);
}

