.c {
    display: block;
    position: relative;
    margin: 5px auto;
    height: auto;
    width: 1000px;
    padding: 20px;
    left: -130px;
}

.c .title {
    font-size: 3em;
    text-align: center;
    border: 5px solid blue;
    padding: .3em .2em;
    border-radius: 4px;
}

.text-light {
    color: var(--light-color)
}

.btnQ {
    padding: .2em 1.7em;
    border: none;
    border-radius: .1em;
    font-size: 1.2em;
    color: #202020;
    text-decoration: none;
    background-color: #faff5a;
    width: 500px;
    height: 50px;
}

.next {
    background-color: #faff5a;
    justify-self: flex-end;
}

.prev {
    background-color: #faff5a;
    justify-self: flex-start;
}