.Card {
    width: 380px;
    height: 380px;
    background-color: white;
    padding: 40px;
    border-radius: 35px;
    margin-right: 30px;
}

.profile {
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    color: #007DFC;
}

.name {
    font-weight: 600;
    font-size: 24px;
    line-height: 24px;
    color: black;
    padding: 4px 0 10px 0;
}

.desc {
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    color: #494949;
    text-align: justify;

}

.img {
    width: 70px;
    height: 70px;
    border: 4px solid #007DFC;
    border-radius: 50%;
    margin-bottom: 6px;
}

.button {
    background: #007DFC;
    border-radius: 10px;
    padding: 6px 20px;
    border: none;
    margin-top: 10px;
    color: #fff;
    font-weight: 600;
    width: 300px;
}