
 * {
     font-family: 'Ubuntu', sans-serif;
     font-style: normal;
 }

 .css{
        background: #111827;
        height: 100vh;
        width: 100px;
        overflow: hidden;
        margin: 0;
        padding: 0;
  }


.h{
    font-weight: 800;
    font-size: 48px;
    line-height: 56px;
    color: white;
}
.p{
    justify-content: center;
    align-items: left;
    font-weight: 200;
    font-size: 24px;
    line-height: 28px;
    color: white;
}

.aturGambar {
    position: absolute;
    width: 50px;
    height: 400px;
    left: 750px;
    top: 20px;
}

img {
    width: 80%;
    height: 400px;
}


@media screen and (max-width: 1200px) {
   
.aturGambar {
        position: relative;
        left: -50px;
        top: 25px;
    }
   
    img {
            width: 500px;
            height: 400px;
        }
}

