

.containerSNB {
    display: inline-flex;
    overflow: hidden;
}


.main {
    width: 100%;
    padding: 20px;
}

.sidebar {
    background-color: black;
    color: black;
    height: 90vh;
}

.top_section {
    display: flex;
    align-items: center;
    padding: 20px 15px;

    
}

.logo {
    font-size: 30px;
    background-color: #000;
 
    
}

.bars {
    color: white;
    display: flex;
    font-size: 25px;
    margin-left: 20px;
    
}

.liP {
    display: flex;
    color: black;
    padding: 10px 15px;
    gap: 15px;
    transition: all 0.5s;
    text-decoration: none;
    background-color: black;
}

.liP:hover {
    background: rgba(245, 245, 245, 0.169);
    color: #000;
    transition: all 0.5s;
    
}

.active {
    background: #007DFC;
}

.icon{
    font-size: 20px;
    
}

.link_text {
    color: black;
    font-size: 20px;
    
}

.activated {
    color: #09bef0;
    
}
