

.containerSNB {
    display: inline-flex;
    overflow: hidden;
}


.main {
    width: 100%;
    padding: 20px;
}

.sidebarP {
    background-color: #000;
    color: #007DFC;
    height: 125vh;
}

.top_section {
    display: flex;
    align-items: center;
    padding: 20px 15px;
    color: #007DFC;

}

.logo {
    font-size: 30px;
    color: #007DFC;
}

.barsP {
    display: flex;
    font-size: 25px;
    margin-left: 20px;
    color: #007DFC;
   
}

.linkP {
    display: flex;
    color: #007DFC;
    padding: 10px 15px;
    gap: 15px;
    transition: all 0.5s;
    text-decoration: none;
    background-color: white;
    border-style: solid;
    border-color: #000;
    margin-bottom: 5px;
       
}

.linkP:hover {
    background: #007DFC;
    transition: all 0.5s;
}

.active {
    background: #007DFC;
}

.icon{
    font-size: 20px;
    color: black;
}

.icon:hover {
    font-size: 20px;
    color: white;
}

.link_text {
    font-size: 20px;
    color:black;
}

.activated {
    color: #09bef0;
}

